<template>
  <div class="Mewtwo-wrap">
    <div class="MewtwoLocationPoint"></div>
    <div
      class="Mewtwo"
      :class="{'hideMewtwo':electricPower,'lightningHideMewtwo':lightningHideMewtwo,'MewtwoNone':MewtwoNone}"
    ></div>
    <div
      class="MewtwoImg"
      :class="{'animation':charred==false,'charred':charred==true,'MewtwoNone':MewtwoNone}"
    ></div>
    <div
      class="MewtwoLightningStrikes"
      :class="{'showLightningStrikes':!electricPower,'MewtwoNone':MewtwoNone}"
    ></div>

    <div class="Mindpower-img" :class="{'showTheMindpowerActive':showTheMindpowerActive}"></div>
    <div class="MindpowerActive" :class="{'MewtwoNone':lightningHideMewtwo}"></div>
    <div
      class="MindpowerToStopTheThunder"
      :class="{'hideMindpowerToStopTheThunder':hideMindpowerToStopTheThunder}"
    ></div>
  </div>
</template> 

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      MewtwoPosition: 650,
      MewtwoImgPosition: 580,
      lightningHideMewtwo: false,
      showTheMindpowerActive: false,
      hideMindpowerToStopTheThunder: false,
      charred: false,
      MewtwoNone: false,
      MewtwoNoneNum: 0
    };
  },
  computed: {
    ...mapState([
      "isMoveAvtive",
      "runSpeed",
      "switchToStartGame",
      "isRolesRunning",
      "electricPower",
      "Mewtwo"
    ])
  },
  watch: {
    // switchToStartGame(v) {
    // 按las's go按鈕啟動機制
    //   if (v) {
    //     this.randomTheMewtwo();
    //   }
    // }
    // 定時啟動機制
    Mewtwo() {
      this.randomTheMewtwo();
    }
  },
  methods: {
    randomTheMewtwo() {
      // this.MewtwoNoneNum = Math.floor(Math.random() * (9 - 0 + 1) + 0);
      // console.log("超夢" + this.MewtwoNoneNum);
      // if (this.MewtwoNoneNum == 1) {
      //   this.MewtwoNone = true;
      // } else {
      //   this.MewtwoNone = false;
      // }
      this.startTheMewtwo();
    },
    startTheMewtwo() {
      let vm = this;
      let MewtwoLocationPoint = document.querySelector(".MewtwoLocationPoint");
      let Mewtwo = document.querySelector(".Mewtwo");
      let MewtwoImg = document.querySelector(".MewtwoImg");
      let MewtwoLightningStrikes = document.querySelector(
        ".MewtwoLightningStrikes"
      );
      let MindpowerActive = document.querySelector(".MindpowerActive");
      let MindpowerToStopTheThunder = document.querySelector(
        ".MindpowerToStopTheThunder"
      );
      vm.MewtwoTime = setInterval(() => {
        this.MewtwoPosition -= this.runSpeed + 1;
        this.MewtwoImgPosition -= this.runSpeed + 1;
        MewtwoLocationPoint.style.left = this.MewtwoPosition + "px";
        Mewtwo.style.left = this.MewtwoPosition - 60 + "px";
        MewtwoImg.style.left = this.MewtwoImgPosition + "px";
        MewtwoLightningStrikes.style.left = this.MewtwoImgPosition - 8 + "px";
        MindpowerActive.style.left = this.MewtwoImgPosition + 100 + "px";
        MindpowerToStopTheThunder.style.left =
          this.MewtwoImgPosition + 120 + "px";
        this.moveJudge();
        this.LightningStrikesJudge();
        this.MindpowerActiveJudge();
        this.MindpowerToStopTheThunderJudge();
        if (this.MewtwoPosition < -100) {
          clearInterval(vm.MewtwoTime);
          // this.MewtwoPosition = 650;
          // this.MewtwoImgPosition = 580;
          // this.lightningHideMewtwo = false;
          // this.hideMindpowerToStopTheThunder = false;
          // this.charred = false;
          // this.randomTheMewtwo();
          this.switchShowTheMewtwo(false);
        }
        if (!this.isMoveAvtive) {
          clearInterval(vm.MewtwoTime);
        }
      }, 50);
    },
    moveJudge() {
      let vm = this;
      let Mewtwo = document.querySelector(".Mewtwo");
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let MewtwoLeft = Mewtwo.offsetLeft;
      let MewtwoRight = MewtwoLeft + Mewtwo.offsetWidth;
      let MewtwoTop = Mewtwo.offsetTop;
      let MewtwoBottom = MewtwoTop + Mewtwo.offsetHeight;
      if (
        raichuRight < MewtwoLeft ||
        MewtwoRight < raichuLeft ||
        raichuBottom < MewtwoTop ||
        MewtwoBottom < raichuTop
      ) {
        return;
      } else {
        // 碰撞到障礙物，背景停止移動
        vm.chengeTheStyAvtive();
        // 碰撞到障礙物，角色停止動作
        vm.stopTheRunningActive();
        // 碰撞到障礙物，障礙物停止移動
        clearInterval(vm.MewtwoTime);
      }
    },
    LightningStrikesJudge() {
      let vm = this;
      let MewtwoLightningStrikes = document.querySelector(
        ".MewtwoLightningStrikes"
      );
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let MewtwoLightningStrikesLeft = MewtwoLightningStrikes.offsetLeft;
      let MewtwoLightningStrikesRight =
        MewtwoLightningStrikesLeft + MewtwoLightningStrikes.offsetWidth;
      let MewtwoLightningStrikesTop = MewtwoLightningStrikes.offsetTop;
      let MewtwoLightningStrikesBottom =
        MewtwoLightningStrikesTop + MewtwoLightningStrikes.offsetHeight;
      if (
        raichuRight < MewtwoLightningStrikesLeft ||
        MewtwoLightningStrikesRight < raichuLeft ||
        raichuBottom < MewtwoLightningStrikesTop ||
        MewtwoLightningStrikesBottom < raichuTop
      ) {
        return;
      } else {
        this.lightningHideMewtwo = true;
        this.hideMindpowerToStopTheThunder = true;
        this.charred = true;
      }
    },
    MindpowerActiveJudge() {
      let vm = this;
      let MindpowerActive = document.querySelector(".MindpowerActive");
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let MindpowerActiveLeft = MindpowerActive.offsetLeft;
      let MindpowerActiveRight =
        MindpowerActiveLeft + MindpowerActive.offsetWidth;
      let MindpowerActiveTop = MindpowerActive.offsetTop;
      let MindpowerActiveBottom =
        MindpowerActiveTop + MindpowerActive.offsetHeight;
      if (
        raichuRight < MindpowerActiveLeft ||
        MindpowerActiveRight < raichuLeft ||
        raichuBottom < MindpowerActiveTop ||
        MindpowerActiveBottom < raichuTop
      ) {
        return;
      } else {
        //碰到障礙物，顯示念力攻擊 X 1
        this.showTheMindpowerActive = true;
        setTimeout(() => {
          this.showTheMindpowerActive = false;
        }, 1000);
      }
    },
    MindpowerToStopTheThunderJudge() {
      let vm = this;
      let MindpowerToStopTheThunder = document.querySelector(
        ".MindpowerToStopTheThunder"
      );
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let MindpowerToStopTheThunderLeft = MindpowerToStopTheThunder.offsetLeft;
      let MindpowerToStopTheThunderRight =
        MindpowerToStopTheThunderLeft + MindpowerToStopTheThunder.offsetWidth;
      let MindpowerToStopTheThunderTop = MindpowerToStopTheThunder.offsetTop;
      let MindpowerToStopTheThunderBottom =
        MindpowerToStopTheThunderTop + MindpowerToStopTheThunder.offsetHeight;
      if (
        raichuRight < MindpowerToStopTheThunderLeft ||
        MindpowerToStopTheThunderRight < raichuLeft ||
        raichuBottom < MindpowerToStopTheThunderTop ||
        MindpowerToStopTheThunderBottom < raichuTop
      ) {
        return;
      } else {
        // 碰撞到障礙物，背景停止移動
        vm.chengeTheStyAvtive();
        // 碰撞到障礙物，角色停止動作
        vm.stopTheRunningActive();
        // 碰撞到障礙物，障礙物停止移動
        clearInterval(vm.MewtwoTime);
      }
    },
    ...mapMutations([
      "chengeTheStyAvtive",
      "stopTheRunningActive",
      "switchShowTheMewtwo"
    ])
  },
  mounted() {
    let MewtwoTime;
    let MewtwoLightningStrikesTime;
    this.randomTheMewtwo();
  }
};
</script>

<style lang="scss" scoped>
// 超夢-圖片
.MewtwoImg {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom center;
  background-image: url(./assets/img/Mewtwo01.png);
  position: absolute;
  left: 650px;
  bottom: 160px;
  width: 60px;
  height: 55px;
  // border: 2px solid #000000;
}

// 超夢-移動效果
.MewtwoImg.animation {
  animation: MewtwoRunning 0.8s infinite steps(1, start);
}

@keyframes MewtwoRunning {
  0% {
    background-image: url(./assets/img/Mewtwo01.png);
  }
  25% {
    background-image: url(./assets/img/Mewtwo02.png);
  }
  50% {
    background-image: url(./assets/img/Mewtwo01.png);
  }
  75% {
    background-image: url(./assets/img/Mewtwo03.png);
  }
  5% {
    background-image: url(./assets/img/Mewtwo01.png);
  }
}
// 超夢-電擊後改變顯示圖片
.MewtwoImg.charred {
  background-image: url(./assets/img/charredMewtwo.png);
}

// 超夢-碰觸判定區域
.Mewtwo {
  position: absolute;
  bottom: 170px;
  width: 45px;
  height: 40px;
  z-index: 50;
  // border: 2px solid red;
}

// 超夢-電擊的瞬間+被電擊後移除碰觸判定+被電擊後移除念力攻擊碰觸判定
.Mewtwo.hideMewtwo,
.Mewtwo.lightningHideMewtwo,
.MindpowerActive.MewtwoNone {
  display: none;
}

// 超夢-電擊判定區域
.MewtwoLightningStrikes {
  position: absolute;
  bottom: 168px;
  width: 75px;
  height: 65px;
  z-index: 50;
  border: 2px solid orange;
}

// 超夢-平常的時候隱藏電擊判定區域
.showLightningStrikes {
  display: none;
}

// 超夢-位置定位
.MewtwoLocationPoint {
  width: 10px;
  height: 10px;
  // background: orange;
  position: absolute;
  // left: 5px;
  top: 10px;
}

//念力攻擊-效果顯示圖
.Mindpower-img {
  width: 150px;
  height: 180px;
  position: absolute;
  bottom: 10px;
  left: 35px;
  background-image: url(./assets/img/Mindpower.gif);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  // border: 2px solid pink;
  z-index: 1000;
  display: none;
}

.Mindpower-img.showTheMindpowerActive {
  display: block;
}

//念力攻擊-觸發判定
.MindpowerActive {
  left: 750px;
  width: 10px;
  height: 180px;
  position: absolute;
  bottom: 10px;
  // background-color: pink;
  z-index: 1000;
}

//念力攻擊-遊戲終止判定區
.MindpowerToStopTheThunder {
  left: 770px;
  width: 10px;
  height: 180px;
  position: absolute;
  bottom: 10px;
  // border: 1px solid pink;
  z-index: 1000;
}
//念力攻擊-移除遊戲終止判定區
.Mewtwo-wrap .MindpowerToStopTheThunder.hideMindpowerToStopTheThunder {
  display: none;
}

// 超夢-隱藏設定
.Mewtwo-wrap .Mewtwo.MewtwoNone,
.Mewtwo-wrap .MewtwoImg.MewtwoNone,
.Mewtwo-wrap .MewtwoLightningStrikes.MewtwoNone {
  display: none;
}
</style>


