<template>
  <div class="seaSide-wrap">
    <div class="seaSide-sky avtive-sky" :class="{'stop-avtive-sky':!isMoveAvtive}"></div>
    <div class="seaSide-sea"></div>
    <!-- <div class="seaSide-grass avtive-grass"></div> -->
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState(["isMoveAvtive"])
  }
};
</script>

<style lang="scss" scoped>
.seaSide-wrap {
  width: 100%;
  height: 240px;
  overflow: hidden;
  position: relative;
}
.seaSide-sky {
  width: 1500px;
  height: 300px;
  background-image: url(./assets/img/seasideSky.png);
  background-position: bottom left;
  background-size: contain;
  background-repeat: repeat;
  position: absolute;
  left: 0;
  bottom: 0;
}
.avtive-sky {
  animation-name: moveSky;
  animation-duration: 40s;
  animation-fill-mode: none;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.seaSide-sky.stop-avtive-sky {
  animation-play-state: paused;
}

@keyframes moveSky {
  0% {
    left: 0;
  }
  100% {
    left: -637px;
  }
}

.seaSide-sea {
  width: 1500px;
  height: 61px;
  background-image: url(./assets/img/seasideSea.png);
  background-position: bottom left;
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  left: 0;
  bottom: 0;
}

// .seaSide-grass {
