<template>
  <div class="Pikachu-wrap">
    <div class="Pikachu"></div>
    <div class="PikachuImg" :class="{'animation':PikachuRun}"></div>
    <div class="bubble" v-if="MarkMain">
      <div class="exclamation-Mark animated bounceIn" v-if="MarkContent =='exclamation'"></div>
      <div class="heart-Mark animated bounceIn" v-if="MarkContent =='heart'"></div>
    </div>
    <div class="passGame" v-if="showPassGame">恭喜過關</div>
  </div>
</template> 

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      PikachuPosition: 650,
      PikachuImgPosition: 650,
      PikachuRun: true,
      MarkMain: true,
      MarkContent: "exclamation",
      showPassGame: false
    };
  },
  computed: {
    ...mapState([
      "isMoveAvtive",
      "runSpeed",
      "switchToStartGame",
      "isRolesRunning",
      "Pikachu"
    ])
  },
  watch: {
    //  switchToStartGame(v) {
    //   if (v) {
    //     this.randomThePikachu();
    //   }
    // }
    Pikachu(v) {
      if (v) {
        this.randomThePikachu();
      }
    }
  },
  methods: {
    randomThePikachu() {
      this.startThePikachu();
      this.activeMeetThsPikachu();
      setTimeout(() => {
        this.MarkMain = false;
      }, 3000);
    },
    startThePikachu() {
      let vm = this;
      let Pikachu = document.querySelector(".Pikachu");
      let PikachuImg = document.querySelector(".PikachuImg");
      vm.PikachuTime = setInterval(() => {
        this.PikachuPosition -= this.runSpeed + 1;
        this.PikachuImgPosition -= this.runSpeed + 1;
        Pikachu.style.left = this.PikachuPosition - 10 + "px";
        PikachuImg.style.left = this.PikachuImgPosition + "px";
        this.moveJudge();
        if (!this.isMoveAvtive) {
          clearInterval(vm.PikachuTime);
        }
      }, 50);
    },
    moveJudge() {
      let vm = this;
      let Pikachu = document.querySelector(".Pikachu");
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let PikachuLeft = Pikachu.offsetLeft;
      let PikachuRight = PikachuLeft + Pikachu.offsetWidth;
      let PikachuTop = Pikachu.offsetTop;
      let PikachuBottom = PikachuTop + Pikachu.offsetHeight;
      if (
        raichuRight < PikachuLeft ||
        PikachuRight < raichuLeft ||
        raichuBottom < PikachuTop ||
        PikachuBottom < raichuTop
      ) {
        return;
      } else {
        // 碰到雷丘，停止跑步的狀態
        this.PikachuRun = false;
        // // 碰撞到障礙物，背景停止移動
        // vm.chengeTheStyAvtive();
        // // 碰撞到障礙物，角色停止動作
        // vm.stopTheRunningActive();
        // 碰撞到障礙物，障礙物停止移動
        clearInterval(vm.PikachuTime);
        this.MarkMain = true;
        this.MarkContent = "heart";
        setTimeout(() => {
          this.showPassGame = true;
        }, 1000);
      }
    },
    ...mapMutations([
      "chengeTheStyAvtive",
      "stopTheRunningActive",
      "activeMeetThsPikachu"
    ])
  },
  mounted() {
    let PikachuTime;
  }
};
</script>

<style lang="scss" scoped>
@import "./assets/scss/animate.css";
// 皮卡丘-圖片
.PikachuImg {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom center;
  background-image: url(./assets/img/Pikachu02.png);
  position: absolute;
  left: 650px;
  bottom: 24px;
  width: 40px;
  height: 40px;
  // border: 2px solid #000000;
}

// 皮卡丘-移動效果
.PikachuImg.animation {
  animation: PikachuRunning 1s infinite steps(1, start);
}
@keyframes PikachuRunning {
  0% {
    background-image: url(./assets/img/Pikachu01.png);
  }
  25% {
    background-image: url(./assets/img/Pikachu02.png);
  }
  50% {
    background-image: url(./assets/img/Pikachu03.png);
  }
  75% {
    background-image: url(./assets/img/Pikachu02.png);
  }
  100% {
    background-image: url(./assets/img/Pikachu01.png);
  }
}

// 皮卡丘-碰撞判定區塊
.Pikachu {
  position: absolute;
  left: 650px;
  bottom: 29px;
  width: 26px;
  height: 30px;
  z-index: 1000;
  // border: 2px solid orange;
}

.bubble {
  position: absolute;
  bottom: 65px;
  left: 60px;
  width: 60px;
  height: 50px;
  background-image: url(./assets/img/bubble.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
}
.exclamation-Mark {
  width: 25px;
  height: 25px;
  background-image: url(./assets/img/Exclamation-Mark.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}
.heart-Mark {
  width: 25px;
  height: 25px;
  background-image: url(./assets/img/heart.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}
.passGame {
  position: absolute;
  top: 40%;
  left: 40%;
  padding: 10px 20px;
  border: 2px solid #ffffff;
  background-color: rgba(0, 0, 0, 0.55);
  z-index: 1000;
  color: #ffffff;
  font-weight: bold;
  font-size: 24px;
  border-radius: 20px;
}
</style>


