<template>
  <div class="Fearow-wrap">
    <div class="FearowLocationPoint"></div>
    <div
      class="Fearow"
      :class="{'hideFearow':electricPower,'lightningHideFearow':lightningHideFearow,'FearowNone':FearowNone}"
    ></div>
    <div
      class="FearowImg"
      :class="{'animation':charred==false,'charred':charred==true,'FearowNone':FearowNone}"
    ></div>
    <div
      class="FearowLightningStrikes"
      :class="{'showLightningStrikes':!electricPower,'FearowNone':FearowNone}"
    ></div>
  </div>
</template> 

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      FearowPosition: 905,
      FearowImgPosition: 900,
      lightningHideFearow: false,
      charred: false,
      FearowNone: false,
      FearowNoneNum: 0
    };
  },
  computed: {
    ...mapState([
      "isMoveAvtive",
      "runSpeed",
      "switchToStartGame",
      "isRolesRunning",
      "electricPower",
      "Fearow",
      "stopAllObstacle"
    ])
  },
  watch: {
    // 按las's go按鈕啟動機制
    // switchToStartGame(v) {
    //   if (v) {
    //     this.randomTheFearow();
    //   }
    // },
    // 定時啟動機制
    Fearow() {
      this.randomTheFearow();
    }
  },
  methods: {
    randomTheFearow() {
      this.FearowNoneNum = Math.floor(Math.random() * (2 - 0 + 1) + 0);
      console.log("大嘴雀" + this.FearowNoneNum);
      if (this.FearowNoneNum == 1) {
        this.FearowNone = true;
      } else {
        this.FearowNone = false;
      }
      this.startTheFearow();
    },
    startTheFearow() {
      let vm = this;
      let FearowLocationPoint = document.querySelector(".FearowLocationPoint");
      let Fearow = document.querySelector(".Fearow");
      let FearowImg = document.querySelector(".FearowImg");
      let FearowLightningStrikes = document.querySelector(
        ".FearowLightningStrikes"
      );
      vm.FearowTime = setInterval(() => {
        this.FearowPosition -= this.runSpeed + 2.5;
        this.FearowImgPosition -= this.runSpeed + 2.5;
        FearowLocationPoint.style.left = this.FearowPosition + "px";
        Fearow.style.left = this.FearowPosition + 10 + "px";
        FearowImg.style.left = this.FearowImgPosition + "px";
        FearowLightningStrikes.style.left = this.FearowImgPosition - 8 + "px";
        this.moveJudge();
        this.LightningStrikesJudge();
        if (this.FearowPosition < -100 && !this.stopAllObstacle) {
          clearInterval(vm.FearowTime);
          this.FearowPosition = 905;
          this.FearowImgPosition = 900;
          this.lightningHideFearow = false;
          this.charred = false;
          this.randomTheFearow();
        }
        if (!this.isMoveAvtive) {
          clearInterval(vm.FearowTime);
        }
      }, 10);
    },
    moveJudge() {
      let vm = this;
      let Fearow = document.querySelector(".Fearow");
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let FearowLeft = Fearow.offsetLeft;
      let FearowRight = FearowLeft + Fearow.offsetWidth;
      let FearowTop = Fearow.offsetTop;
      let FearowBottom = FearowTop + Fearow.offsetHeight;
      if (
        raichuRight < FearowLeft ||
        FearowRight < raichuLeft ||
        raichuBottom < FearowTop ||
        FearowBottom < raichuTop
      ) {
        return;
      } else {
        // 碰撞到障礙物，背景停止移動
        vm.chengeTheStyAvtive();
        // 碰撞到障礙物，角色停止動作
        vm.stopTheRunningActive();
        // 碰撞到障礙物，障礙物停止移動
        clearInterval(vm.FearowTime);
      }
    },
    LightningStrikesJudge() {
      let vm = this;
      let FearowLightningStrikes = document.querySelector(
        ".FearowLightningStrikes"
      );
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let FearowLightningStrikesLeft = FearowLightningStrikes.offsetLeft;
      let FearowLightningStrikesRight =
        FearowLightningStrikesLeft + FearowLightningStrikes.offsetWidth;
      let FearowLightningStrikesTop = FearowLightningStrikes.offsetTop;
      let FearowLightningStrikesBottom =
        FearowLightningStrikesTop + FearowLightningStrikes.offsetHeight;
      if (
        raichuRight < FearowLightningStrikesLeft ||
        FearowLightningStrikesRight < raichuLeft ||
        raichuBottom < FearowLightningStrikesTop ||
        FearowLightningStrikesBottom < raichuTop
      ) {
        return;
      } else {
        this.lightningHideFearow = true;
        this.charred = true;
      }
    },
    ...mapMutations(["chengeTheStyAvtive", "stopTheRunningActive"])
  },
  mounted() {
    let FearowTime;
    let FearowLightningStrikesTime;
    this.randomTheFearow();
  }
};
</script>

<style lang="scss" scoped>
// 大嘴雀-圖片
.FearowImg {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom center;
  background-image: url(./assets/img/Fearow01.png);
  position: absolute;
  left: 900px;
  bottom: 100px;
  width: 60px;
  height: 55px;
  // z-index: 1000;
  // border: 2px solid #000000;
}

// 大嘴雀-移動效果
.FearowImg.animation {
  animation: FearowRunning 0.8s infinite steps(1, start);
}

@keyframes FearowRunning {
  0% {
    background-image: url(./assets/img/Fearow01.png);
  }
  25% {
    background-image: url(./assets/img/Fearow02.png);
  }
  50% {
    background-image: url(./assets/img/Fearow01.png);
  }
  75% {
    background-image: url(./assets/img/Fearow03.png);
  }
  100% {
    background-image: url(./assets/img/Fearow01.png);
  }
}
// 大嘴雀-電擊後改變顯示圖片
.FearowImg.charred {
  background-image: url(./assets/img/charredFearow.png);
}

// 大嘴雀-碰觸判定區域
.Fearow {
  position: absolute;
  left: 905px;
  bottom: 110px;
  width: 30px;
  height: 30px;
  z-index: 1000;
  // border: 2px solid red;
}

// 大嘴雀-電擊的瞬間+被電擊後隱藏碰觸判定
.Fearow.hideFearow,
.Fearow.lightningHideFearow {
  display: none;
}

// 大嘴雀-電擊判定區域
.FearowLightningStrikes {
  position: absolute;

  left: 890px;
  bottom: 95px;
  width: 75px;
  height: 65px;
  z-index: 1000;
  // border: 2px solid orange;
}

// 大嘴雀-平常的時候隱藏電擊判定區域
.showLightningStrikes {
  display: none;
}

// 大嘴雀-位置定位
.FearowLocationPoint {
  width: 10px;
  height: 10px;
  // background: orange;
  position: absolute;
  left: 900px;
  top: 10px;
}

// 大嘴雀-隱藏設定
.Fearow-wrap .Fearow.FearowNone,
.Fearow-wrap .FearowImg.FearowNone,
.Fearow-wrap .FearowLightningStrikes.FearowNone {
  display: none;
}
</style>


