<template>
  <div>
    <div class="PowerIndication" :class="{'noPower':thePowerIndication >= 1}"></div>
    <div class="PowerIndication2" :class="{'noPower':thePowerIndication == 2}"></div>
    <div class="countScore">{{totalScore}}</div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState(["totalScore", "thePowerIndication"])
  }
};
</script>

<style lang="scss" scoped>
.countScore {
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 20px;
  font-weight: bold;
  color: #ffffff;
  text-shadow: 0 0 10px #000000, 0 0 10px #000000, 0 0 10px #000000;
}

.PowerIndication {
  position: absolute;
  width: 30px;
  height: 20px;
  top: 10px;
  left: 20px;
  background-image: url(./assets/img/noPower.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}

.PowerIndication.noPower {
  background-image: url(./assets/img/havePower.png);
}

.PowerIndication2 {
  position: absolute;
  width: 30px;
  height: 20px;
  top: 10px;
  left: 55px;
  background-image: url(./assets/img/noPower.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}

.PowerIndication2.noPower {
  background-image: url(./assets/img/havePower.png);
}
</style>


