<template>
  <div class="Sandslash-wrap">
    <div class="SandslashLocationPoint"></div>
    <div class="Sandslash" :class="{'SandslashNone':SandslashNone}"></div>
    <div class="SandslashImg animation" :class="{'SandslashNone':SandslashNone}"></div>
  </div>
</template> 

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      SandslashPosition: 1200,
      SandslashImgPosition: 1200,
      SandslashNum: 0,
      SandslashNone: false,
    };
  },
  computed: {
    ...mapState([
      "isMoveAvtive",
      "runSpeed",
      "switchToStartGame",
      "isRolesRunning"
    ])
  },
  watch: {
    switchToStartGame(v) {
      if (v) {
        this.randomTheSandslash();
      }
    }
  },
  methods: {
    randomTheSandslash() {
      this.SandslashNum = Math.floor(Math.random() * (3 - 0 + 1) + 0);
      if (this.SandslashNum !== 1) {
        this.SandslashNone = false;
      } else {
        this.SandslashNone = true;
      }
      this.startTheSandslash();
    },
    startTheSandslash() {
      let vm = this;
      let SandslashLocationPoint = document.querySelector(
        ".SandslashLocationPoint"
      );
      let Sandslash = document.querySelector(".Sandslash");
      let SandslashImg = document.querySelector(".SandslashImg");
      vm.SandslashTime = setInterval(() => {
        this.SandslashPosition -= this.runSpeed + 2;
        this.SandslashImgPosition -= this.runSpeed + 2;
        SandslashLocationPoint.style.left = this.SandslashPosition + "px";
        Sandslash.style.left = this.SandslashPosition + "px";
        SandslashImg.style.left = this.SandslashImgPosition + "px";
        this.moveJudge();
        if (this.SandslashPosition < -100) {
          clearInterval(vm.SandslashTime);
          this.SandslashPosition = 1200;
          this.SandslashImgPosition = 1200;
          this.randomTheSandslash();
        }
        if (!this.isMoveAvtive) {
          clearInterval(vm.SandslashTime);
        }
      }, 10);
    },
    moveJudge() {
      let vm = this;
      let Sandslash = document.querySelector(".Sandslash");
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let SandslashLeft = Sandslash.offsetLeft;
      let SandslashRight = SandslashLeft + Sandslash.offsetWidth;
      let SandslashTop = Sandslash.offsetTop;
      let SandslashBottom = SandslashTop + Sandslash.offsetHeight;
      if (
        raichuRight < SandslashLeft ||
        SandslashRight < raichuLeft ||
        raichuBottom < SandslashTop ||
        SandslashBottom < raichuTop
      ) {
        return;
      } else {
        // 碰撞到障礙物，背景停止移動
        vm.chengeTheStyAvtive();
        // 碰撞到障礙物，角色停止動作
        vm.stopTheRunningActive();
        // 碰撞到障礙物，障礙物停止移動
        clearInterval(vm.SandslashTime);
      }
    },
    ...mapMutations(["chengeTheStyAvtive", "stopTheRunningActive"])
  },
  mounted() {
    let SandslashTime;
  }
};
</script>

<style lang="scss" scoped>
// 穿山王-圖片
.SandslashImg {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom center;
  background-image: url(./assets/img/Sandslash01.png);
  position: absolute;
  left: 1200px;
  bottom: 26px;
  width: 40px;
  height: 40px;
  // border: 2px solid #000000;
}

// 穿山王-移動效果
.SandslashImg.animation {
  animation: SandslashRunning 1s infinite steps(1, start);
}
@keyframes SandslashRunning {
  0% {
    background-image: url(./assets/img/Sandslash01.png);
  }
  25% {
    background-image: url(./assets/img/Sandslash02.png);
  }
  50% {
    background-image: url(./assets/img/Sandslash03.png);
  }
  75% {
    background-image: url(./assets/img/Sandslash02.png);
  }
  100% {
    background-image: url(./assets/img/Sandslash01.png);
  }
}

// 穿山王-碰撞判定區塊
.Sandslash {
  position: absolute;
  left: 1200px;
  bottom: 29px;
  width: 26px;
  height: 30px;
  z-index: 1000;
  border: 2px solid #000000;
}

// 穿山王-移動位置與隱藏定位點
.SandslashLocationPoint {
  width: 10px;
  height: 10px;
  // background: blue;
  position: absolute;
  top: 10px;
}

.Sandslash-wrap .Sandslash.SandslashNone,
.Sandslash-wrap .SandslashImg.SandslashNone {
  display: none;
}
</style>


