import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        switchToStartGame: false,
        isMoveAvtive: false,
        isRolesRunning: false,
        isRolesJump: false,
        easeOut: true,
        switchOpenObstacle: true,
        electricPower: false,
        thePowerIndication: 0,
        resetTheGame: false,
        runSpeed: 1,
        runSpeedLevel: 0,
        addRunSpeed: false,
        totalScore: 0,
        Ghost: false,
        meetThsPikachu: false,
        Kingler: false,
        Fearow: false,
        Haunter: false,
        Mewtwo: false,
        Pikachu: false,
        stopAllObstacle: false,
    },
    mutations: {
        chengeTheStyAvtive(state) {
            state.isMoveAvtive = !state.isMoveAvtive;
        },
        stopTheRunningActive(state) {
            state.isRolesRunning = !state.isRolesRunning;
        },
        transitionEase(state) {
            state.easeOut = !state.easeOut;
        },
        playTheGame(state) {
            state.resetTheGame = false;
            setTimeout(() => {
                state.resetTheGame = true;
            }, 50);
            setTimeout(() => {
                state.isMoveAvtive = true;
                state.isRolesRunning = true;
                state.isRolesJump = true;
                state.switchToStartGame = true;
                state.thePowerIndication = 2;
                state.totalScore = 0;
                state.runSpeed = 1;
                state.addRunSpeed = false;
                state.Kingler = false;
                state.Fearow = false;
                state.Haunter = false;
                state.Mewtwo = false;
                state.Pikachu = false;
                state.stopAllObstacle = false;
            }, 100);
        },
        countToScore(state) {
            state.totalScore += 10;
        },
        changeRunSpeed(state, v) {
            state.runSpeed = v;
        },
        addTheRunSpeed(state, v) {
            state.addRunSpeed = v;
        },
        changeRunSpeedLevel(state, v) {
            state.runSpeedLevel = v;
        },
        changeSwitchToObstacle(state) {
            state.switchOpenObstacle = !state.switchOpenObstacle;
        },
        electricToShock(state) {
            if (state.thePowerIndication > 0 && state.isMoveAvtive) {
                state.thePowerIndication = state.thePowerIndication - 1;
                //顯示電擊技能效果
                state.electricPower = !state.electricPower;
                //關閉電擊技能效果
                setTimeout(() => {
                    state.electricPower = !state.electricPower;
                }, 600);
                //恢復電擊技能額度
                setTimeout(() => {
                    if (state.isMoveAvtive && state.thePowerIndication < 2) {
                        state.thePowerIndication = state.thePowerIndication + 1;
                    }
                }, 5000);
            } else {
                return
            }
        },
        stopTheGrass(state) {
            state.switchToStartGame = false;;
        },
        showTheGhost(state) {
            state.Ghost = true;
        },
        switchShowTheKingler(state, v) {
            state.Kingler = v;
        },
        switchShowTheFearow(state, v) {
            state.Fearow = v;
        },
        switchShowTheHaunter(state, v) {
            state.Haunter = v;
        },
        switchShowTheMewtwo(state, v) {
            state.Mewtwo = v;
        },
        switchShowThePikachu(state, v) {
            state.Pikachu = v;
        },
        switchShowThestopAllObstacle(state, v) {
            state.stopAllObstacle = v;
        },
        activeMeetThsPikachu(state) {
            state.meetThsPikachu = true;
            state.isRolesRunning = false;
            state.isRolesJump = false;
            state.thePowerIndication = 0;
        },
    },
})

export default store;


