<template>
  <div>
    <div
      id="thunderbolt"
      :class="{'transition-Ease-out':easeOut,'transition-Ease-in':!easeOut,'activePower':electricPower}"
    ></div>
    <div id="raichu" :class="{'transition-Ease-out':easeOut,'transition-Ease-in':!easeOut}"></div>
    <div id="roles" :class="{'transition-Ease-out':easeOut,'transition-Ease-in':!easeOut}">
      <div class="roles-wrap">
        <div class="roles-running" :class="{'animation':isRolesRunning}"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  computed: {
    ...mapState(["isRolesRunning", "easeOut", "electricPower"])
  }
};
</script>

<style lang="scss" scoped>
#roles {
  position: absolute;
  left: 80px;
  bottom: 25px;
  // overflow: hidden;
  transition: all 0.3s;
}

#roles.transition-Ease-out {
  transition-timing-function: cubic-bezier(0.21, 0.4, 0.28, 0.79);
}

#roles.transition-Ease-in {
  transition-timing-function: cubic-bezier(0.55, 0.33, 0.68, 0.54);
}

.roles-wrap {
  width: 55px;
  height: 45px;
  position: relative;
  overflow: hidden;
}

.roles-running {
  position: absolute;
  width: 55px;
  height: 45px;
  background-image: url(./assets/img/thunder01.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  left: 0;
  top: 0;
}
.roles-running.animation {
  animation: theRockRunning 0.6s infinite steps(1, start);
}

@keyframes theRockRunning {
  0% {
    background-image: url(./assets/img/thunder01.png);
  }
  25% {
    background-image: url(./assets/img/thunder02.png);
  }
  50% {
    background-image: url(./assets/img/thunder01.png);
  }
  75% {
    background-image: url(./assets/img/thunder03.png);
  }
  100% {
    background-image: url(./assets/img/thunder01.png);
  }
}

#raichu {
  position: absolute;
  left: 100px;
  bottom: 30px;
  transition: all 0.3s;
  width: 26px;
  height: 35px;
}

#raichu.transition-Ease-out {
  transition-timing-function: cubic-bezier(0.21, 0.4, 0.28, 0.79);
}

#raichu.transition-Ease-in {
  transition-timing-function: cubic-bezier(0.55, 0.33, 0.68, 0.54);
}

#thunderbolt {
  width: 20px;
  height: 20px;
  position: absolute;
  bottom: 38px;
  left: 100px;
  background-image: url(./assets/img/electricPower.gif);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  transform: scaleX(-1);
  opacity: 0;
}
#thunderbolt.transition-Ease-out {
  transition-timing-function: cubic-bezier(0.21, 0.4, 0.28, 0.79);
}

#thunderbolt.transition-Ease-in {
  transition-timing-function: cubic-bezier(0.55, 0.33, 0.68, 0.54);
}

#thunderbolt.activePower {
  opacity: 1;
  animation-name: thunderboltActivePower;
  animation-duration: 0.1s;
  animation-fill-mode: forwards;
  animation-timing-function: linear;
  // animation-iteration-count: infinite;
}

@keyframes thunderboltActivePower {
  0% {
  }

  100% {
    transform: scaleX(-1) scale(4.4);
  }
}
</style>


