<template>
  <div class="Kingler-wrap">
    <div class="KinglerLocationPoint"></div>
    <div
      class="Kingler"
      :class="{'hideKingler':electricPower,'lightningHideKingler':lightningHideKingler,'KinglerNone':KinglerNone}"
    ></div>
    <div
      class="KinglerImg"
      :class="{'animation':charred==false,'charred':charred==true,'KinglerNone':KinglerNone}"
    ></div>
    <div
      class="KinglerLightningStrikes"
      :class="{'showLightningStrikes':!electricPower,'KinglerNone':KinglerNone}"
    ></div>
  </div>
</template> 

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      KinglerPosition: 505,
      KinglerImgPosition: 500,
      lightningHideKingler: false,
      charred: false,
      KinglerNone: false,
      KinglerNoneNum: 0
    };
  },
  computed: {
    ...mapState([
      "isMoveAvtive",
      "runSpeed",
      "switchToStartGame",
      "isRolesRunning",
      "electricPower",
      "Kingler",
      "stopAllObstacle"
    ])
  },
  watch: {
    // 按las's go按鈕啟動機制
    // switchToStartGame(v) {
    //   if (v) {
    //     this.randomTheKingler();
    //   }
    // },
    // 定時啟動機制
    Kingler() {
      this.randomTheKingler();
    }
  },
  methods: {
    randomTheKingler() {
      this.KinglerNoneNum = Math.floor(Math.random() * (2 - 0 + 1) + 0);
      console.log("巨鉗蟹" + this.KinglerNoneNum);
      if (this.KinglerNoneNum == 1) {
        this.KinglerNone = true;
      } else {
        this.KinglerNone = false;
      }
      this.startTheKingler();
    },
    startTheKingler() {
      let vm = this;
      let KinglerLocationPoint = document.querySelector(
        ".KinglerLocationPoint"
      );
      let Kingler = document.querySelector(".Kingler");
      let KinglerImg = document.querySelector(".KinglerImg");
      let KinglerLightningStrikes = document.querySelector(
        ".KinglerLightningStrikes"
      );
      vm.KinglerTime = setInterval(() => {
        this.KinglerPosition -= this.runSpeed + 2;
        this.KinglerImgPosition -= this.runSpeed + 2;
        KinglerLocationPoint.style.left = this.KinglerPosition + "px";
        Kingler.style.left = this.KinglerPosition + "px";
        KinglerImg.style.left = this.KinglerImgPosition + "px";
        KinglerLightningStrikes.style.left = this.KinglerImgPosition - 8 + "px";
        this.moveJudge();
        this.LightningStrikesJudge();
        if (this.KinglerPosition < -100 && !this.stopAllObstacle) {
          clearInterval(vm.KinglerTime);
          this.KinglerPosition = 505;
          this.KinglerImgPosition = 500;
          this.lightningHideKingler = false;
          this.charred = false;
          this.randomTheKingler();
        }
        if (!this.isMoveAvtive) {
          clearInterval(vm.KinglerTime);
        }
      }, 10);
    },
    moveJudge() {
      let vm = this;
      let Kingler = document.querySelector(".Kingler");
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let KinglerLeft = Kingler.offsetLeft;
      let KinglerRight = KinglerLeft + Kingler.offsetWidth;
      let KinglerTop = Kingler.offsetTop;
      let KinglerBottom = KinglerTop + Kingler.offsetHeight;
      if (
        raichuRight < KinglerLeft ||
        KinglerRight < raichuLeft ||
        raichuBottom < KinglerTop ||
        KinglerBottom < raichuTop
      ) {
        return;
      } else {
        // 碰撞到障礙物，背景停止移動
        vm.chengeTheStyAvtive();
        // 碰撞到障礙物，角色停止動作
        vm.stopTheRunningActive();
        // 碰撞到障礙物，障礙物停止移動
        clearInterval(vm.KinglerTime);
      }
    },
    LightningStrikesJudge() {
      let vm = this;
      let KinglerLightningStrikes = document.querySelector(
        ".KinglerLightningStrikes"
      );
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let KinglerLightningStrikesLeft = KinglerLightningStrikes.offsetLeft;
      let KinglerLightningStrikesRight =
        KinglerLightningStrikesLeft + KinglerLightningStrikes.offsetWidth;
      let KinglerLightningStrikesTop = KinglerLightningStrikes.offsetTop;
      let KinglerLightningStrikesBottom =
        KinglerLightningStrikesTop + KinglerLightningStrikes.offsetHeight;
      if (
        raichuRight < KinglerLightningStrikesLeft ||
        KinglerLightningStrikesRight < raichuLeft ||
        raichuBottom < KinglerLightningStrikesTop ||
        KinglerLightningStrikesBottom < raichuTop
      ) {
        return;
      } else {
        this.lightningHideKingler = true;
        this.charred = true;
      }
    },
    ...mapMutations(["chengeTheStyAvtive", "stopTheRunningActive"])
  },
  mounted() {
    let KinglerTime;
    let KinglerLightningStrikesTime;
    this.randomTheKingler();
  }
};
</script>

<style lang="scss" scoped>
// 巨鉗蟹-圖片
.KinglerImg {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom center;
  background-image: url(./assets/img/Kingler01.png);
  position: absolute;
  left: 500px;
  bottom: 25px;
  width: 36px;
  height: 36px;
  // z-index: 1000;
  // border: 2px solid #000000;
}

// 巨鉗蟹-移動效果
.KinglerImg.animation {
  animation: KinglerRunning 0.3s infinite steps(1, start);
}

@keyframes KinglerRunning {
  0% {
    background-image: url(./assets/img/Kingler01.png);
  }
  25% {
    background-image: url(./assets/img/Kingler02.png);
  }
  50% {
    background-image: url(./assets/img/Kingler01.png);
  }
  75% {
    background-image: url(./assets/img/Kingler03.png);
  }
  100% {
    background-image: url(./assets/img/Kingler01.png);
  }
}
// 巨鉗蟹-電擊後改變顯示圖片
.KinglerImg.charred {
  background-image: url(./assets/img/charredKingler.png);
}

// 巨鉗蟹-碰觸判定區域
.Kingler {
  position: absolute;
  left: 505px;
  bottom: 25px;
  width: 25px;
  height: 35px;
  z-index: 1000;
  // border: 2px solid red;
}

// 巨鉗蟹-電擊的瞬間+被電擊後隱藏碰觸判定
.Kingler.hideKingler,
.Kingler.lightningHideKingler {
  display: none;
}

// 巨鉗蟹-電擊判定區域
.KinglerLightningStrikes {
  position: absolute;
  left: 505px;
  bottom: 25px;
  width: 47px;
  height: 47px;
  z-index: 1000;
  // border: 2px solid orange;
}

// 巨鉗蟹-平常的時候隱藏電擊判定區域
.showLightningStrikes {
  display: none;
}

// 巨鉗蟹-位置定位
.KinglerLocationPoint {
  width: 10px;
  height: 10px;
  // background: orange;
  position: absolute;
  left: 500px;
  top: 10px;
}

// 巨鉗蟹-隱藏設定
.Kingler-wrap .Kingler.KinglerNone,
.Kingler-wrap .KinglerImg.KinglerNone,
.Kingler-wrap .KinglerLightningStrikes.KinglerNone {
  display: none;
}
</style>


