<template>
  <div class="RWD-control-wrap">
    <div class="electricShock" @click="electricToShock"></div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      rolesPosition: 25,
      gradient: 0,
      switchStatus: true,
      duratio: 0,
      speed: 0,
      jumpHeightValue: 0,
      jumpStrength: 1,
      transitionDuration: 300,
      keyDownControl: true
    };
  },
  computed: {
    ...mapState(["isRolesJump", "isMoveAvtive"])
  },
  methods: {
    ...mapMutations([
      "stopTheRunningActive",
      "transitionEase",
      "electricToShock"
    ])
  },
  mounted() {
    let vm = this;

    document.onkeydown = function(e) {
      if (e.keyCode == 13) {
        vm.electricToShock();
      }
    };
  }
};
</script>

<style lang="scss" scoped>
.electricShock {
  width: 50px;
  height: 240px;
  border: 1px solid #000000;
  margin-right: 10px;
  // margin-top: 10px;
  border-radius: 15px 0 0 15px;
  background-color: #6c757d;
  box-shadow: 0 0 3px #000000, 0 0 3px #000000;
  background-image: url(./assets/img/LightningStrike.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}

.RWD-control-wrap {
  display: none;
}

@media (max-width: 815px) {
  .RWD-control-wrap {
    display: block;
  }
}
</style>


