<template>
  <div class="RWD-control-wrap">
    <div class="control-wrap">
      <div class="jumpBtnWeak" @click="rolesToJumpWeak"></div>
      <div class="jumpBtnStrong" @click="rolesToJumpStrong"></div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      rolesPosition: 25,
      gradient: 0,
      switchStatus: true,
      duratio: 0,
      speed: 0,
      jumpHeightValue: 0,
      jumpStrength: 1,
      transitionDuration: 300,
      keyDownControl: true
    };
  },
  computed: {
    ...mapState(["isRolesJump", "isMoveAvtive"])
  },
  methods: {
    readyToJump() {
      let vm = this;
      if (this.isRolesJump) {
        vm.accumulationEnergy = setInterval(() => {
          this.jumpHeightValue += 1;
        }, 0);
      } else {
        return;
      }
    },
    rolesToJump() {
      let vm = this;
      if (this.jumpHeightValue >= 50) {
        this.jumpStrength = 170;
        this.transitionDuration = 400;
        roles.style.transitionDuration = this.transitionDuration + "ms";
        raichu.style.transitionDuration = this.transitionDuration + "ms";
        thunderbolt.style.transitionDuration = this.transitionDuration + "ms";
      } else {
        this.jumpStrength = 120;
        this.transitionDuration = 300;
        roles.style.transitionDuration = this.transitionDuration + "ms";
        raichu.style.transitionDuration = this.transitionDuration + "ms";
        thunderbolt.style.transitionDuration = this.transitionDuration + "ms";
      }
      this.jumpHeightValue = 0;
      clearInterval(vm.accumulationEnergy);

      if (this.switchStatus && this.isRolesJump && this.isMoveAvtive) {
        this.switchStatus = false;
        this.rolesJumpUpActive();
      } else {
        return;
      }
    },

    rolesToJumpWeak() {
      let vm = this;
      this.jumpStrength = 120;
      this.transitionDuration = 300;
      roles.style.transitionDuration = this.transitionDuration + "ms";
      raichu.style.transitionDuration = this.transitionDuration + "ms";
      thunderbolt.style.transitionDuration = this.transitionDuration + "ms";

      this.jumpHeightValue = 0;
      clearInterval(vm.accumulationEnergy);

      if (this.switchStatus && this.isRolesJump && this.isMoveAvtive) {
        this.switchStatus = false;
        this.rolesJumpUpActive();
      } else {
        return;
      }
    },

    rolesToJumpStrong() {
      let vm = this;

      this.jumpStrength = 170;
      this.transitionDuration = 400;
      roles.style.transitionDuration = this.transitionDuration + "ms";
      raichu.style.transitionDuration = this.transitionDuration + "ms";
      thunderbolt.style.transitionDuration = this.transitionDuration + "ms";

      this.jumpHeightValue = 0;
      clearInterval(vm.accumulationEnergy);

      if (this.switchStatus && this.isRolesJump && this.isMoveAvtive) {
        this.switchStatus = false;
        this.rolesJumpUpActive();
      } else {
        return;
      }
    },

    rolesJumpUpActive() {
      this.stopTheRunningActive();
      roles.style.bottom = this.jumpStrength + "px";
      raichu.style.bottom = this.jumpStrength + 5 + "px";
      thunderbolt.style.bottom = this.jumpStrength + 13 + "px";
      setTimeout(() => {
        this.rolesJumpDownActive();
        this.transitionEase();
      }, this.transitionDuration + 50);
    },

    rolesJumpDownActive() {
      let vm = this;
      roles.style.bottom = 25 + "px";
      raichu.style.bottom = 30 + "px";
      thunderbolt.style.bottom = 38 + "px";
      setTimeout(() => {
        vm.stopTheRunningActive();
        this.switchStatus = true;
        this.transitionEase();
      }, this.transitionDuration + 50);
    },
    ...mapMutations(["stopTheRunningActive", "transitionEase"])
  },
  mounted() {
    //  角色綁定
    let vm = this;
    let roles = document.querySelector("#roles");
    let raichu = document.querySelector("#raichu");
    let thunderbolt = document.querySelector("#thunderbolt");
    let rockToRunning = document.querySelector(".rock-running");
    // 跳躍動作計時變數
    let rolesJumpUp, rolesJumpDown, accumulationEnergy;

    document.onkeypress = function(e) {
      if (e.keyCode == 32) {
        if (vm.keyDownControl) {
          vm.readyToJump();
          vm.keyDownControl = false;
        } else {
          return;
        }
      }
    };

    document.onkeyup = function(e) {
      if (e.keyCode == 32) {
        vm.rolesToJump();
        vm.keyDownControl = true;
      }
    };

    // document.onkeydown = function(e) {
    //   if (e.keyCode == 13) {
    //     vm.electricToShock();
    //   }
    // };
  }
};
</script>

<style lang="scss" scoped>
.jumpBtnWeak {
  width: 50px;
  height: 115px;
  border: 1px solid #000000;
  margin-bottom: 8px;
  border-radius: 0 15px 0 0;
  background-color: #6c757d;
  box-shadow: 0 0 3px #000000, 0 0 3px #000000;
  background-image: url(./assets/img/jumpBtnImg.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}

.jumpBtnStrong {
  width: 50px;
  height: 115px;
  border: 1px solid #000000;
  border-radius: 0 0 15px 0;
  background-color: #6c757d;
  box-shadow: 0 0 3px #000000, 0 0 3px #000000;
  background-image: url(./assets/img/jumpBtnImg2.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}

.control-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  margin-left: 10px;
}
.RWD-control-wrap {
  display: none;
}

@media (max-width: 815px) {
  .RWD-control-wrap {
    display: block;
  }
}
</style>


