<template>
  <div>
    <div class="letsGo" v-if="!playTheGameSwitch" @click="playTheGame">Let's Go</div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      playTheGameSwitch: false
    };
  },
  watch: {
    isMoveAvtive(v) {
      this.playTheGameSwitch = v;
    }
  },
  computed: {
    ...mapState(["isMoveAvtive"])
  },
  methods: {
    ...mapMutations(["playTheGame"])
  },
};
</script>

<style lang="scss" scoped>
.letsGo {
  position: absolute;
  top: 40%;
  left: 40%;
  padding: 10px 20px;
  border: 2px solid #ffffff;
  background-color: rgba(0, 0, 0, 0.55);
  z-index: 1000;
  color: #ffffff;
  font-weight: bold;
  font-size: 24px;
  cursor: pointer;
  border-radius: 20px;
}
</style>


