<template>
  <div>
    <div id="app" v-show="isNotUpright">
      <ControlLightningStrikes />
      <div class="app-wrap">
        <PlayGameBtn />
        <SeasideBg />
        <Grassland />
        <ObstacleGeodude v-if="resetTheGame" />
        <ObstacleKingler v-if="Kingler" />
        <ObstacleFearow v-if="Fearow" />
        <ObstacleHaunter v-if="Haunter" />
        <ObstacleMewtwo v-if="Mewtwo"/>
         <ObstaclePikachu v-show="Pikachu" />
        <!-- <ObstacleSandslash v-if="resetTheGame" /> -->
        <!-- <ObstacleGengar v-if="Ghost" /> -->
        <RolesThunder />
        <Dashboard />
      </div>
      <ControlToJump />
      <!-- <ControlGroup /> -->
    </div>
    <div class="remindScreen-wrap" v-show="!isNotUpright">
      <div class="remindScreen"></div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import SeasideBg from "./SeasideBg";
import Grassland from "./Grassland";
import RolesThunder from "./RolesThunder";
import ControlLightningStrikes from "./ControlLightningStrikes";
import ControlToJump from "./ControlToJump";
import Dashboard from "./Dashboard";
import PlayGameBtn from "./PlayGameBtn";

import ObstacleGeodude from "./ObstacleGeodude";
import ObstacleKingler from "./ObstacleKingler";
import ObstacleFearow from "./ObstacleFearow";
import ObstacleHaunter from "./ObstacleHaunter";
import ObstacleMewtwo from "./ObstacleMewtwo";
import ObstacleSandslash from "./ObstacleSandslash";
import ObstaclePikachu from "./ObstaclePikachu";
// import ObstacleGengar from "./ObstacleGengar";
// import ObstacleTree from "./ObstacleTree";
// import ControlGroup from "./ControlGroup";
export default {
  data() {
    return {
      isNotUpright: false
    };
  },
  components: {
    SeasideBg,
    Grassland,
    RolesThunder,
    // ControlGroup,
    ControlLightningStrikes,
    ControlToJump,
    // ObstacleTree,
    ObstacleKingler,
    ObstacleFearow,
    ObstacleGeodude,
    ObstacleHaunter,
    // ObstacleGengar,
    ObstacleSandslash,
    ObstaclePikachu,
    ObstacleMewtwo,
    Dashboard,
    PlayGameBtn
  },
  computed: {
    ...mapState([
      "resetTheGame",
      "Ghost",
      "Kingler",
      "Fearow",
      "Haunter",
      "Mewtwo",
      "Pikachu"
    ])
  },
  mounted() {
    const widthView = document.documentElement.clientWidth;
    const heightView = document.documentElement.clientHeight;
    if (widthView > heightView) {
      this.isNotUpright = true;
    }
  }
};
</script>


<style lang="scss" scoped>
@import "./assets/scss/reset";
#app {
  font-family: "微軟正黑體";
  user-select: none;
  max-width: 650px;
  margin: calc((100vh -240px) / 5) auto 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.app-wrap {
  width: 100%;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 5px #000000, 0 0 5px #000000, 0 0 5px #000000, 0 0 5px #000000;
  border-radius: 20px;
}

.remindScreen-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80vh;
}
.remindScreen {
  background-image: url(./assets/img/remindScreen.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  width: 80%;
  height: 80%;
}

@media (max-width: 815px) {
  #app {
    margin: calc((100vh -240px) / 9) auto 0;
  }
  .app-wrap {
    border-radius: 0px;
  }
}

@media (max-width: 740px) {
  #app {
    margin: calc((100vh -240px) / 5) auto 0;
  }
  .app-wrap {
    border-radius: 0px;
  }
}

@media (max-width: 670px) {
  #app {
    margin: calc((100vh -240px) / 6) auto 0;
  }
  .app-wrap {
    border-radius: 0px;
  }
}

@media (max-width: 570px) {
  #app {
    margin: calc((100vh -240px) / 8) auto 0;
  }
  .app-wrap {
    border-radius: 0px;
  }
}
</style>



